import { Component, OnInit, ViewChild, ChangeDetectorRef, ViewEncapsulation } from '@angular/core';
import { UserService } from 'src/app/api/controllers/User';
import { ScreenService } from 'src/app/api/controllers/Screen';
import { Router, ActivatedRoute } from '@angular/router';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';

import * as moment from 'moment';
import _moment from "moment-timezone";
import { CondoAnimations } from 'src/@condo/animations';

@Component({
  selector: 'app-add-role',
  templateUrl: './add-role.component.html',
  styleUrls: ['./add-role.component.scss'],
  encapsulation  : ViewEncapsulation.None,
  animations   : CondoAnimations
})
export class AddRoleComponent implements OnInit {
  @ViewChild('rolesForm') form:any;
  allRoles: Array<any> = [];
  isCreate:boolean=true;
  roleInfo:any = {};
  message:any = null;
  constructor(private userService: UserService,
    private router: Router,
    private activatedRoute : ActivatedRoute,
    private sessionService : SessionService,
    private sharedService : SharedService,
    private _changeDetectorRef: ChangeDetectorRef
  ) { }

  addRole() {
    this.message = null;
    if(!this.form.valid){
      window.scroll({
        top: 0,
        behavior: 'smooth'
      });
      // Show the validation message
      this.message = {
        appearance: 'outline',
        content   : "Fill the Required Fields",
        shake     : true,
        showIcon  : true,
        type      : 'error'
      };
      //Mark for check
      this._changeDetectorRef.markForCheck();
    } else {
      let addRoleParam = {
        "roleId": 0,
        "apartmentId": this.sessionService.apartmentId,
        "roleName": this.roleInfo.roleName,
        "description": this.roleInfo.description,
        "isActive": true,
        "insertedBy": this.sessionService.userId,
        "insertedOn": _moment().toISOString(),
        "updatedBy": null,
        "updatedOn": null,
        "roleTypeId": this.roleInfo.roleTypeId
      }
      let roleParam = {
        role: addRoleParam
      }
      this.userService.addRole(roleParam).subscribe((resp:any) => {
        if(resp.message) {
          this.router.navigate(['roles-type-list'],{relativeTo:this.activatedRoute.parent})
          this.sharedService.openSnackBar('Role Added Successfully','success');
        } else 
          this.sharedService.openSnackBar(resp.errorMessage,'error');
      },(error)=>{
        this.sharedService.openSnackBar('Server Error','error');
      })
    }
  }

  updateRole() {
    this.message = null;
    if(!this.form.valid) {
      window.scroll({
        top: 0,
        behavior: 'smooth'
      });
      // Show the validation message
      this.message = {
        appearance: 'outline',
        content   : "Fill the Required Fields",
        shake     : true,
        showIcon  : true,
        type      : 'error'
      };
      //Mark for check
      this._changeDetectorRef.markForCheck();
    } else {
      let addRoleParam = {
        "roleId":  this.roleInfo.roleId,
        "apartmentId": this.sessionService.apartmentId,
        "roleName": this.roleInfo.roleName,
        "description": this.roleInfo.description,
        "isActive": true,
        "insertedBy": this.roleInfo.insertedBy,
        "insertedOn": this.roleInfo.insertedOn,
        "updatedBy": this.sessionService.userId,
        "updatedOn": _moment().toISOString(),
        "roleTypeId": this.roleInfo.roleTypeId
      }
      let roleParam = {
        role: addRoleParam
      }
      this.userService.updateRole(roleParam).subscribe((resp:any) => {
        if(resp.message) {
          this.router.navigate(['roles-type-list'],{relativeTo:this.activatedRoute.parent})
          this.sharedService.openSnackBar(resp.message,'success');
        } else 
          this.sharedService.openSnackBar(resp.errorMessage,'error');
      },(error)=>{
        this.sharedService.openSnackBar('Server Error','error');
      })
    }
  }

  ngOnInit(): void {
    if (this.activatedRoute.params['value'].id != undefined) {
      this.isCreate = false;
      let queryParamBase = {
        roleId: this.activatedRoute.params['value'].id
      };
      this.userService.getRoleByRoleId(queryParamBase).subscribe(resp =>{
        this.roleInfo = resp[0];
      })
    }
    this.userService.getAllRoleTypes().subscribe((resp: any) => {
      if(Array.isArray(resp)) {
        this.allRoles = resp.filter(data => {
          if (data.value == 1 || data.value == 3) {
            return data;
          }
        })
      }
    })
  }
}
