import { Component, OnInit, ChangeDetectorRef, ViewChild, ViewEncapsulation } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ScreenService } from 'src/app/api/controllers/Screen';
import { UserService } from 'src/app/api/controllers/User';
import { SessionService } from 'src/app/core/session/session.service';

import * as moment from 'moment';
import _moment from "moment-timezone";
import { CondoAnimations } from 'src/@condo/animations';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-configure-roles',
  templateUrl: './configure-roles.component.html',
  styleUrls: ['./configure-roles.component.scss'],
  encapsulation  : ViewEncapsulation.None,
  animations   : CondoAnimations
})
export class ConfigureRolesComponent implements OnInit {
  @ViewChild('rolesForm') form:any;
  allRoles: Array<any> = [];
  roleList:any = [];
  roleTypeId: number;
  roleId:number;
  name: any;
  description: any;
  menuRoleSecLevelId:any;
  isCreate:boolean=true;
  menuRole:any;
  message:any = null;
  constructor(private userService: UserService,
    private router: Router,
    private activatedRoute : ActivatedRoute,
    private sessionService : SessionService,
    private sharedService : SharedService,
    private _changeDetectorRef: ChangeDetectorRef,
    private screenService: ScreenService) { }

  setRoleType(event) {
    this.roleId = null;
    this.roleTypeId = event[0].value;
    this.getRoleList();
  }

  getRoleList() {
    //Admin Role Type
    var paramsRoles = {
      RoleTypeId: this.roleTypeId,
      ApartmentId: this.sessionService.apartmentId
    }
    this.userService.getAllRolesByRoleTypeIdByApartmentId(paramsRoles).subscribe((res: any) => {
      this.roleList = res;
    });
  }

  setRole(event) {
    this.roleId = event[0].roleId;
  }

  addRole() {
    this.message = null;
    if(!this.form.valid){
      window.scroll({
        top: 0,
        behavior: 'smooth'
      });
      // Show the validation message
      this.message = {
        appearance: 'outline',
        content   : "Fill the Required Fields",
        shake     : true,
        showIcon  : true,
        type      : 'error'
      };
      //Mark for check
      this._changeDetectorRef.markForCheck();
    } else {
      let addMenuLevelParam = {
        "menuSecLevelId": 0,
        "apartmentId": this.sessionService.apartmentId,
        "name": this.name,
        "isActive": true,
        "insertedBy": this.sessionService.userId,
        "insertedOn": _moment().toISOString(),
        "updatedBy": null,
        "updatedOn": null
      }
      let secLevelParam = {
        seclevel: addMenuLevelParam
      }
      this.screenService.addMenuSecLevel(secLevelParam).subscribe((resp:any) => {
        if (resp && resp.message) {
          let addMenuRoleSecLevel = {
            "menuRoleSecLevelId": 0,
            "apartmentId": this.sessionService.apartmentId,
            "roleId": this.roleId,
            "secLevelId": resp.message,
            "isActive": true,
            "insertedBy": this.sessionService.userId,
            "insertedOn": _moment().toISOString(),
            "updatedBy": null,
            "updatedOn": null
          }
          let roleSecLevelParam = {
            roleseclevel: addMenuRoleSecLevel
          }
          this.screenService.addMenuRoleSecLevel(roleSecLevelParam).subscribe((resp:any) => {
            if(resp.message) {
              this.sharedService.openSnackBar('Menu Control Added Successfully','success');
              this.router.navigate(['/ams/roles-permissions/role-permissions-list']);
            } else {
              this.sharedService.openSnackBar(resp.errorMessage,'error')
            }
          },(error)=>{
            this.sharedService.openSnackBar('Server Error','error');
          })
        }
      })
    }
  }

  updateRole(){
    this.message = null;
    if(!this.form.valid){
      window.scroll({
        top: 0,
        behavior: 'smooth'
      });
      // Show the validation message
      this.message = {
        appearance: 'outline',
        content   : "Fill the Required Fields",
        shake     : true,
        showIcon  : true,
        type      : 'error'
      };
      //Mark for check
      this._changeDetectorRef.markForCheck();
    } else {
      let updataParam = {
        "menuSecLevelId": this.menuRole.secLevelId,
        "apartmentId":  this.sessionService.apartmentId,
        "name": this.name,
        "isActive": true,
        "insertedBy": this.menuRole.insertedBy,
        "insertedOn": this.menuRole.insertedOn,
        "updatedBy": this.sessionService.userId,
        "updatedOn": _moment().toISOString()
      }
      let updateRoleParam = {
        menuSecLevel : updataParam
      }
      this.screenService.updateMenuSecLevel(updateRoleParam).subscribe((resp:any) =>{
        if(resp.message) {
          this.sharedService.openSnackBar('Menu Control Updated','success');
          this.router.navigate(['/ams/roles-permissions/role-permissions-list']);
        } else {
          this.sharedService.openSnackBar(resp.errorMessage,'error')
        }
      },(error)=>{
        this.sharedService.openSnackBar('Server Error','error');
      })
    }
  }

  ngOnInit(): void {
    if (this.activatedRoute.params['value'].id != undefined) {
      this.menuRoleSecLevelId = this.activatedRoute.params['value'].id;
      this.isCreate = false;
      let queryParamBase = {
        apartmentId: this.sessionService.apartmentId,
        menuRoleSecLevelId: this.menuRoleSecLevelId,
      };
      this.screenService.getMenuRoleSecLevelbyRoleSecLevelId(queryParamBase).subscribe(resp =>{
        this.menuRole = resp[0];
        this.roleId = this.menuRole.roleId;
        this.name = this.menuRole.secLevelName;
        this.roleTypeId = this.menuRole.roleTypeId;
        this.getRoleList();
      })
    }
    this.userService.getAllRoleTypes().subscribe((resp: any) => {
      if(Array.isArray(resp)) {
        this.allRoles = resp.filter(data => {
          if (data.value == 1 || data.value == 3) {
            return data;
          }
        })
      }
    })
  }
}
