import { HostListener, Injector, ViewChild } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ScreenService } from 'src/app/api/controllers/Screen';
import { UserService } from 'src/app/api/controllers/User';
import { SessionService } from 'src/app/core/session/session.service';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { ModalService } from 'src/app/shared/services/modal.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-role-type-list',
  templateUrl: './role-type-list.component.html',
  styleUrls: ['./role-type-list.component.scss']
})
export class RoleTypeListComponent implements OnInit {

  public roleHeader: Array<any> = [];
  public roleTypeList: any;
  public isEnableCreate: boolean = true;
  modalService: any;
  isShowRoleList:boolean=false;
  roleTypeSearch:string = '';
  apiSubscribe:Subscription;

  constructor(private router: Router,
    private userService: UserService,
    private sharedService: SharedService,
    private sessionService: SessionService,
    private injector: Injector) {
    this.modalService = this.injector.get(ModalService);
  }

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  @HostListener('window:onEditRole', ['$event.detail'])
  onEditRole(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    let roleId = dataRecord.roleId;
    this.router.navigateByUrl('/ams/roles-permissions/edit-role-type/' + roleId);
  }

  @HostListener('window:onDeleteRole', ['$event.detail'])
  onDeleteRole(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let roleId = dataRecord.roleId
    this.modalService.showConfirmModal(roleId);
  }

  onGlSearchFilter(event) {
    if(event != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = event;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.roleHeader.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  getRolePermissionList() {
    this.isShowRoleList = false;
    let param = {
      ApartmentId: this.sessionService.apartmentId,
      roleTypeIDs: '1,3,5',
    }
    this.userService.getAllRolesbyMultipleRoleTypeId(param).subscribe((resp: any) => {
      let refMenuData = resp.filter(data => {
        return data.isActive
      });
      let gridSourceData = {
        localdata: refMenuData,
        datatype: "array"
      }
      this.roleTypeList = new jqx.dataAdapter(gridSourceData);
      this.isShowRoleList = true;
      this.isEnableCreate = true;
    })
  }

  ngOnInit(): void {
    this.getRolePermissionList();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.roleHeader = [{
        text: 'Role Type',
        datafield: 'roleTypeName',
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer,
        minwidth: 120
      },{
        text: 'role name',
        datafield: 'roleName',
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer,
        minwidth: 120
      }, {
        text: 'description',
        datafield: 'description',
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer,
        minwidth: 120
      },{
        text: 'action',
        datafield: 'roleTypeId',
        cellsalign: 'center',
        align: 'center',
        width: 120,
        cellsrenderer: (row,column,value): string => {
          if(value != 5) {
          
            return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="onEditRole(' + row + ')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a>'
            +'<a href="javascript:void(0)" class="mr-2" onClick="onDeleteRole(' + row + ')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a>'
            +'</div>'
          } else {
            return ''
          }
        },
        renderer: columnrenderer
    }];
   
    this.apiSubscribe = this.sharedService.unitlistdeleteindexcast.subscribe(id => {
      if (id != null) {
        var params = {
          roleId: id,
          deleteBy: this.sessionService.userId,
        }
        this.userService.deleteRole(params).subscribe((res: any) => {
          this.sharedService.setUnitListDeleteIndex(null);
          if(res.message) {
            this.getRolePermissionList();
            this.sharedService.openSnackBar('Role deleted Successfully','success');
          } else {
            this.sharedService.openSnackBar(res.errorMessage,'error');
          }
        }, error => {
          this.sharedService.setUnitListDeleteIndex(null);
          this.sharedService.openSnackBar('Server Error','error');
        });
      }
    })
  }

  ngOnDestroy(): void {
    this.apiSubscribe.unsubscribe();
  }

}

function onEditRole(row) {
  var event = new CustomEvent('onEditRole', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).onEditRole = onEditRole;

function onDeleteRole(row) {
  var event = new CustomEvent('onDeleteRole', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).onDeleteRole = onDeleteRole;
